body {
  background-color: #143642;
  font-family: "Ubuntu", sans-serif;
}

.container {
  width: 70%;
  height: auto;
  margin: 100px auto;
  background-color: rgb(15, 29, 34);
  border: 2px solid rgb(38, 70, 83);
  -webkit-box-shadow: 0 8px 6px -6px black;
  -moz-box-shadow: 0 8px 6px -6px black;
  box-shadow: 0 8px 6px -6px black;
}
.first-row {
  width: 100%;
  margin: 0 auto;
  background-color: rgb(15, 29, 34);
  height: 3rem;

  font-size: 1.1em;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border-top: 3px solid rgb(38, 70, 83);
}
.second-row {
  margin: 0 auto;
  border-bottom: 2px solid rgb(38, 70, 83);
  height: 3em;
  line-height: 250%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.item-content {
  width: 20%;
  height: 100%;
  float: left;
  text-align: center;
  color: rgb(86, 207, 225);
  display: flex;
  justify-content: center;
  align-items: center;
}

.first-row > .item-content {
  align-items: flex-end;
}

.disabled {
  cursor: default;
  pointer-events: none;
  opacity: 0.4;
}

.disabled:hover {
  cursor: default;
  pointer-events: none;
}

.last-row {
  width: 100%;
  box-sizing: border-box;
  border-top: 2px solid rgb(38, 70, 83);
  min-height: 2em;
  /* overflow: overlay; */
  /* bottom: 0; */
  /* text-align: center; */
  font-size: 2em;
  font-weight: 800;
  color: rgb(255, 102, 179);
  display: flex;

  align-items: center;
  font-family: "Ubuntu", sans-serif;
}

input[type="text"] {
  background-color: rgba(38, 70, 83, 0.8);

  color: rgb(86, 207, 225);
  border-radius: 3px;
  border: 1px solid rgb(86, 207, 225);
  height: 2.3em;
  width: 90%;
  font-size: 1em;
  margin: 0;
}

input[type="number"] {
  background-color: rgba(38, 70, 83, 0.8);
  color: rgb(86, 207, 225);
  border-radius: 3px;
  border: 1px solid rgb(86, 207, 225);
  height: 2em;
  width: 40%;
  text-align: center;
  font-size: 1.2em;
  margin: 0;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: rgba(86, 207, 225, 0.1);
  font-family: "Ubuntu", sans-serif;
}

::-moz-placeholder {
  /* Firefox 19+ */
  color: rgba(86, 207, 225, 0.1);
}

input[type="text"]:focus {
  outline: 1px solid rgb(255, 198, 255);
}

input[type="number"]:focus {
  outline: 1px solid rgb(255, 198, 255);
}
input[type="color"] {
  height: 2em;
}
button {
  border: none;
  background-color: rgb(15, 29, 34);
  cursor: pointer;
}

.fa-plus,
.fa-trash {
  font-size: 1.5em;
}

button:focus {
  outline: none;
}

.item-content > input[type="color"],
.item-content > input[type="text"],
.item-content > input[type="number"] {
  position: relative;
}

.third-row {
  /* min-height: 4em; */

  overflow: overlay;
}

.activity {
  width: 80%;
  height: 80%;
  margin: 10px auto 0 auto;
  position: relative;
  border: 1px solid rgb(86, 207, 225);
  border-radius: 40px;
  display: flex;

  align-items: center;
  bottom: 10px;
}

.activity:first-child {
  margin-top: 20px;
}

.active {
  animation: pulse 1s infinite;
  border: 1px solid rgb(255, 102, 179);
}

.activity-name,
.activity-duration,
.activity-position,
.duplicate,
.remove {
  width: 15%;
  height: 100%;
  background-color: rgba(15, 29, 34, 0);
  float: left;
  font-size: 1.1em;
  text-align: center;
  font-family: "Ubuntu", sans-serif;
  color: rgb(86, 207, 225);
  display: flex;
  justify-content: center;
  align-items: center;
}

.activity-position i:hover {
  cursor: pointer;
}

.activity-name {
  width: 55%;
}

.activity-name input {
  text-align: center;
}

.duplicate,
.remove {
  margin: 0 15px;
}

.duplicate:hover,
.remove:hover {
  color: rgb(71, 182, 199);
}

.angle:hover {
  color: yellowgreen;
}

.angle:active {
  transform: translateY(1px);
}

.player {
  position: relative;
  float: left;

  align-items: center;
  height: 100%;
}

.play,
.pause,
.undo {
  color: rgb(86, 207, 225);
  text-shadow: 1px 1.5px 1px rgba(65, 130, 157, 1);
}

.play:active,
.pause:active,
.undo:active {
  color: rgb(86, 186, 225);
  text-shadow: 0px 0px 0px rgba(65, 130, 157, 1);
  transform: translateY(1px);
}
.play:hover,
.pause:hover,
.undo:hover {
  color: rgb(35, 174, 195);
}

.addition {
  text-shadow: 1px 1px 0px rgba(123, 164, 98, 1);
  font-size: 2.5em;
  color: yellowgreen;
}

.addition:active {
  color: rgb(123, 164, 40);
  text-shadow: 0px 0px 0px rgb(18, 19, 19);
  transform: translateY(1px);
}

.addition:hover {
  color: rgb(148, 194, 56);
}

h1.title {
  text-align: center;
  margin-top: 2em;
  color: yellowgreen;
}

@keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(255, 102, 179, 0.4);
  }

  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(255, 102, 179, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(255, 102, 179, 0);
  }
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(86, 207, 225, 0.4);
  }

  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(86, 207, 225, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(86, 207, 225, 0);
  }
}

/* css for the drodown list */

.custom-select-wrapper {
  position: relative;
  user-select: none;
  width: 40%;
  margin: 0 auto;
  top: 50px;
}
.custom-select {
  position: relative;
  display: flex;
  flex-direction: column;
  border-width: 0 2px 0 2px;
  border-style: solid;
  border-color: rgb(15, 29, 34);
}
.custom-select__trigger {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 22px;
  font-size: 20px;
  font-weight: 300;
  color: rgb(255, 102, 179);
  height: 60px;
  line-height: 40px;
  background: rgb(15, 29, 34);
  cursor: pointer;
  border-width: 2px 0 2px 0;
  border-style: solid;
  border-color: rgb(15, 29, 34);
}
.custom-options {
  position: absolute;

  top: 100%;
  left: 0;
  right: 0;
  border: 2px solid rgb(15, 29, 34);
  border-top: 0;
  background: rgb(38, 70, 83);
  transition: all 0.5s;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 2;
}
.custom-select.open .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.custom-option {
  position: relative;
  display: block;
  padding: 0 22px 0 22px;
  font-size: 22px;
  font-weight: 300;
  color: yellowgreen;
  line-height: 40px;
  cursor: pointer;
  transition: all 0.5s;
  width: 80%;
  display: inline-block;
}
.workout-item:hover,
.save-to-file:hover,
.load-to-file:hover,
.create-item:hover {
  cursor: pointer;
  background-image: linear-gradient(
    to left,
    rgba(54, 116, 110, 0),
    rgb(0, 0, 0)
  );
}

.arrow {
  position: relative;
  height: 15px;
  width: 15px;
}
.arrow::before,
.arrow::after {
  content: "";
  position: absolute;
  bottom: 0px;
  width: 0.15rem;
  height: 100%;
  transition: all 0.5s;
}
.arrow::before {
  left: -5px;
  transform: rotate(45deg);
  background-color: rgb(255, 102, 179);
}
.arrow::after {
  left: 5px;
  transform: rotate(-45deg);
  background-color: rgb(255, 102, 179);
}
.open .arrow::before {
  left: -5px;
  transform: rotate(-45deg);
}
.open .arrow::after {
  left: 5px;
  transform: rotate(45deg);
}

.create-item {
  border-top: 2px solid #0f1d22;
  width: 100%;
  color: rgb(255, 102, 179);
  display: flex;
  align-items: center;
}

.save-to-file,
.load-to-file {
  width: 100%;
  color: rgb(255, 102, 179);
  display: flex;
  align-items: center;
}

/* end of dropdown */
.workout-item {
  height: 100%;
  display: flex;
  align-items: center;
}

.delete-workout {
  box-sizing: border-box;
  display: inline-block;
  float: right;
  width: 20%;
  text-align: center;
  height: 100%;
  align-items: center;
  text-align: center;
}

.delete-workout:hover {
  color: rgb(206, 55, 130);
}

.Header {
  background-color: #143642;
  color: rgb(86, 207, 225);
  text-align: center;
  height: 40px;
  width: 100%;
}

.title-workout {
  display: inline-block;
  float: left;
  width: 80%;
  height: 100%;
}

.title-workout > h1 > input {
  width: 100%;
  height: 100%;
  display: block;
  border: none;
  background-color: #143642;
  color: rgb(62, 148, 161);
  padding-left: 10px;
}

.title-workout > h1 > input:focus {
  box-shadow: inset 0 0 10px rgb(206, 55, 130);
  outline: none;
}

.title-workout > h1 {
  width: 100%;
  height: 100%;
}

h5 {
  color: rgb(86, 207, 225);
  right: 10px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
}

#timer {
  width: 70%;
  margin: 0 auto;
  text-align: center;
}

.save-workout {
  width: 20%;
  display: inline-block;
  float: right;
  font-size: 1.2em;
  position: relative;
  padding: 10px 30px;
  height: 100%;
  -moz-box-shadow: inset 0 0 10px #000000;
  -webkit-box-shadow: inset 0 0 10px #000000;
  box-shadow: inset 0 0 10px #000000;
  border-left: 1px solid rgb(38, 70, 83);
  user-select: none;
  text-align: center;
}

.save-workout:hover {
  cursor: pointer;
  -moz-box-shadow: inset 0 0 5px #000000;
  -webkit-box-shadow: inset 0 0 5px #000000;
  box-shadow: inset 0 0 5px #000000;
}

.save-workout:active {
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  box-shadow: inset 0 0 5px rgb(255, 102, 179);
}

@media only screen and (max-width: 1130px) {
  .custom-select-wrapper {
    width: 50%;
  }
}

@media only screen and (max-width: 960px) {
  #timer {
    width: 100%;
    margin-top: 10px;
  }

  #timer > h1 {
    text-align: center;
    width: auto;
  }

  .player {
    top: 50px;
    height: 0;
  }

  .last-row {
    display: table;
    height: 3.5em;
  }

  h5 {
    text-align: right;
    display: block;
    top: 10px;
  }

  .total-time {
    display: block;
    width: 60%;
    position: relative;
    float: right;
  }
}

@media only screen and (max-width: 720px) {
  .container {
    width: 90%;
    font-size: 0.8em;
  }

  .last-row {
    height: 4em;
  }

  .activity {
    width: 80%;
    height: 100%;

    border: 1px solid rgb(86, 207, 225);
    border-radius: 40px;
  }

  .save-workout {
    width: 20%;
    padding: 10px 20px;
  }

  .activity-position {
    padding-bottom: 5px;
  }

  .title-workout > h1 > input {
    font-size: 1.1em;
  }

  .custom-select__trigger,
  .custom-option {
    line-height: 20px;
    padding: 15px 5px;
  }

  .custom-select-wrapper {
    width: 90%;
  }
}
